.ecommerce-why-redkite-right-subbox-button{
    border-radius:30px ;
    border: 3px solid $main-title-and-text-color;
    padding: 12px 55px;
    background-color: $main-white-color;
    font-size: $main-fontSize-for-text;
    font-weight: bold;
    margin-bottom: 140px;
}

@media only screen and (max-width: 1366px) {
    .ecommerce-why-redkite-right-subbox-button{
        padding: 10px 40px;
        margin-bottom: 100px;
        font-size: 18px;
    }
}